.settings {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 0 20px;
}

.packages-settings {
    width: 80%;
    height: auto;
    padding: 0 20px;
    box-shadow: inset;
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0;
    padding: 0;
}

.packages-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    padding: 0 20px;
}

.package-list-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    height: auto;
    padding: 0 20px;
    margin: 10px 0;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 10px;
    box-shadow: inset;
    background-color: rgb(255, 255, 255);
    filter: drop-shadow(0 0 0.2rem rgb(0, 0, 0, 0.2));
}

.pkg-meta-text {
    font-size: 1rem;
    font-weight: 400;
    margin: 0;
    padding: 2px;
}

.pkg-meta-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: auto;
    padding: 0 20px;
}

.pkg-title {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0;
    padding: 0;
}


/* CSS */
.pkg-action-btn {
    background: #FF4742;
    border: 1px solid #FF4742;
    border-radius: 6px;
    box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    font-family: nunito, roboto, proxima-nova, "proxima nova", sans-serif;
    font-size: 16px;
    font-weight: 800;
    line-height: 16px;
    min-height: 40px;
    outline: 0;
    padding: 12px 14px;
    text-align: center;
    text-rendering: geometricprecision;
    text-transform: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: middle;
}

.pkg-action-btn:hover,
.pkg-action-btn:active {
    background-color: initial;
    background-position: 0 0;
    color: #FF4742;
    background: #FFFFFF;
}

.pkg-action-btn:active {
    opacity: .5;
}

.uninstall-buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    padding: 0 20px;
}

.uninstall-prompt-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.package-installer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
}

.pkg-install-btn{
    padding-top: 10px;
}

.pkg-install-header{
    padding: 5px; 
    margin: 0px;
}

.pkg-install-subheader{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center; 
}

.pkg-git-input{
    width: 100%;
    height: 30px;
    border-radius: 5px;
    border: 1px solid #ccc;
    padding: 5px;
    margin: 5px;
}

input[type="text"]::placeholder {
    text-align: center;
}